h1, h2 {
  hyphens: none;
}
h1 {
  font-size: 1.3rem;
  text-align: center;
  margin-top: 1em;
}
h2 {
  font-size: 1.2rem;
  font-variant: small-caps;
  border-bottom: dashed 1px #eee;
  margin: 2em 0 -.5em 0;
  letter-spacing: 1px;
}
h3 {
  font-size: 1.05rem;
  margin-bottom: -.75rem;
  text-align: center;
}
